import Slide from "@/components/transitions/Slide";

import ClickOutside from 'vue-click-outside'

export default {
  name: "BaseSelect",
  components: {Slide},
  data: () => ({
    showOptions: false,
  }),
  props: {
    placeholder: {
      type: String,
      default: "Выберите из списка"
    },
    // title ( top )
    title: {
      type: String,
      default: ""
    },
    value: {
      type: [Object, String, Number, null],
      default: null
    },
    // options list ( for select )
    options: {
      type: Array,
      default: () => []
    },
    // name ( name )
    nameSpace: {
      type: String,
      default: "name"
    },
    // key ( id )
    keySpace: {
      type: String,
      default: "id"
    },
    // error message
    error: {
      type: String,
      default: ""
    }
  },
  computed: {
    selectedTitle() {
      if (!this.value) return this.placeholder;
      if (typeof this.value === "object") return this.value[this.nameSpace];
      return this.value;
    }
  },
  methods: {
    outClick() {
      if (this.showOptions) this.showOptions = false;
    },
    isSelected(option) {
      if (!this.value) return false;
      return this.value[this.keySpace] === option[this.keySpace];
    },
    select(option) {
      if (this.value && option[this.keySpace].toString() === this.value[this.keySpace].toString()) return null;
      this.$emit("input", option);
      this.hideOptions();
    },
    toggleOptions() {
      this.showOptions = !this.showOptions;
    },
    hideOptions() {
      this.showOptions = false;
    }
  },
  directives: {
    ClickOutside
  }
}
