export default {
  name: "Slide",
  inheritAttrs: false,
  methods: {
    enter(element) {
      element.style = null;
      const height = getComputedStyle(element).height;
      element.style.height = 0;
      requestAnimationFrame(() => {
        element.style.height = height;
      });
    },
    afterEnter(element) {
      element.style.height = 'auto';
    },
    leave(element) {
      const height = getComputedStyle(element).height;
      element.style.height = height;
      requestAnimationFrame(() => {
        element.style.height = 0;
      });
    }
  }
}
